@import '../common/styles/sass/variables';
@import '../common/styles/sass/mixins';

.mainBlock {
  height: 100vh;
  position: relative;

  &::after {
    content: "";
    background: url('../assets/image/background.jpeg');
    background-size: cover;
    opacity: 0.4;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
  }

  .photo {
    width: 100%;
    height: 90%;
    background: url('../assets/image/background.jpeg');
    background-size: cover;
    box-shadow: 0 3px 8px 0 rgba(15, 15, 20, 0.2);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    .about {
      width: 100%;
      height: 10%;
      padding: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;

      h1 {
        @include text(50px, 0, $frontPageTitleColor);
        font-weight: 800;
        letter-spacing: 1px;
      }

      .text {
        @include text(30px, 1, $frontPageTitleColor);
        @include block(0, 0, 0, 0);
        font-weight: 500;
        letter-spacing: 1px;
      }

      @media screen and (max-width: 400px) {
        h1 {
          @include text(40px, 1.2, $frontPageTitleColor);
          font-weight: 700;
        }
        .text {
          @include text(25px, 1, $frontPageTitleColor);
          font-weight: 400;
        }
      }
      @media screen and (max-width: 703px) and (min-width: 401px) {
        h1 {
          @include text(50px, 1, $frontPageTitleColor);
        }
      }
    }
  }
}




